<h1 id="语义理解" tabindex="-1"><a class="header-anchor" href="#语义理解" aria-hidden="true">#</a> 语义理解</h1>
<blockquote>
<p>貌似此接口已经下线，调用无正确返回值</p>
</blockquote>
<ul>
<li>
<p><code>query($keyword, $categories, $optional = [])</code> 语义理解:</p>
<ul>
<li><code>$keyword</code> 为关键字</li>
<li><code>$categories</code> 需要使用的服务类型，多个用 “,” 隔开字符串，不能为空;</li>
<li><code>$optional</code> 为其它属性：
<ul>
<li><code>latitude</code>  <code>float</code>  纬度坐标，与经度同时传入；与城市二选一传入</li>
<li><code>longitude</code>  <code>float</code>  经度坐标，与纬度同时传入；与城市二选一传入</li>
<li><code>city</code>   <code>string</code>  城市名称，与经纬度二选一传入</li>
<li><code>region</code> <code>string</code>  区域名称，在城市存在的情况下可省；与经纬度二选一传入</li>
<li><code>uid</code>  <code>string</code> 用户唯一id（非开发者id），用户区分公众号下的不同用户（建议填入用户openid），如果为空，则无法使用上下文理解功能。appid和uid同时存在的情况下，才可以使用上下文理解功能。</li>
</ul>
</li>
</ul>
</li>
</ul>
<blockquote>
<p>注：单类别意图比较明确，识别的覆盖率比较大，所以如果只要使用特定某个类别，建议将 category 只设置为该类别。</p>
</blockquote>
<p>示例：</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$result</span> <span class="token operator">=</span> <span class="token variable">$app</span><span class="token operator">-></span><span class="token property">semantic</span><span class="token operator">-></span><span class="token function">query</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'查一下明天从北京到上海的南航机票'</span><span class="token punctuation">,</span> <span class="token string double-quoted-string">"flight,hotel"</span><span class="token punctuation">,</span> <span class="token keyword">array</span><span class="token punctuation">(</span><span class="token string single-quoted-string">'city'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'北京'</span><span class="token punctuation">,</span> <span class="token string single-quoted-string">'uid'</span> <span class="token operator">=></span> <span class="token string single-quoted-string">'123456'</span><span class="token punctuation">)</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">// 查询参数：</span>
<span class="token comment">// {</span>
<span class="token comment">//    "query":"查一下明天从北京到上海的南航机票",</span>
<span class="token comment">//    "city":"北京",</span>
<span class="token comment">//    "category": "flight,hotel",</span>
<span class="token comment">//    "appid":"wxaaaaaaaaaaaaaaaa",</span>
<span class="token comment">//    "uid":"123456"</span>
<span class="token comment">// }</span>
